.button {
    --color-primary-light: #590016;
    --color-secondary-light: #590016;
    --color-disabled-light: #9b9b9b;
  
    --color-primary-dark: #fff;
    --color-secondary-dark: #dfdeff;
    --color-text-dark: var(--color-primary-light);
    --color-disabled-dark: #d9d8d9;
    --color-disabled-text-dark: #4a4a4a;
  
    --color-white: #fff;
    --font-family: "IBM Plex Mono", Monaco, monospace;
  
    font-family: var(--font-family);
    padding: 10px 15px !important; /* tamaño cuadrado de los botones*/
    font-weight: 500 !important;
    font-size: 16px !important; /* tamaño fuente botones*/
    border-radius: 2px;
    margin: 10px 0px;
    cursor: pointer;
    transition: background-color 0.1s linear, color 0.1s linear,
      border 0.1s linear, outline 0.1s linear !important;
  }
  
  /* Main Button Styles */
  
  .button--primary {
    border: 2px solid transparent;
  }
  
  .button--secondary {
    background: none;
  }
  
  .button--tertiary {
    border: 2px solid transparent;
    width: 120px;
    padding: 10px 0px !important;
    background: none;
  }
  
  .button.button--primary:disabled,
  .button.button--primary:disabled:hover,
  .button.button--primary:disabled:focus,
  .button.button--secondary:disabled,
  .button.button--secondary:disabled:hover,
  .button.button--secondary:disabled:focus,
  .button.button--tertiary:disabled,
  .button.button--tertiary:disabled:hover,
  .button.button--tertiary:disabled:focus {
    cursor: not-allowed;
  }
  
  .button.button--secondary:disabled,
  .button.button--secondary:disabled:hover,
  .button.button--secondary:disabled:focus,
  .button.button--tertiary:disabled,
  .button.button--tertiary:disabled:hover,
  .button.button--tertiary:disabled:focus {
    background: none;
  }
  
  .button.button--tertiary:disabled,
  .button.button--tertiary:disabled:hover,
  .button.button--tertiary:disabled:focus {
    border: none;
  }
  
  .button.button--primary.button--icon {
    padding: 10px 45px;
  }
  
  .button__icon {
    width: 15px;
    height: 15px;
    margin: 0px 10px 0px 0px;
  }
  
  /* Light Theme Button Styles */
  
  .button--light.button--primary {
    background-color: var(--color-primary-light);
    color: var(--color-white);
  }
  
  .button--light .button__icon > path {
    fill: var(--color-white);
  }
  
  .button--light.button--primary:hover,
  .button--light.button--primary:focus {
    background-color: var(--color-secondary-light);
  }
  
  .button--light.button--primary:focus,
  .button--light.button--secondary:focus {
    outline: 2px solid var(--color-secondary-light);
    outline-offset: 4px;
  }
  
  .button--light.button--secondary {
    color: var(--color-primary-light);
    border: 2px solid var(--color-primary-light);
  }
  
  .button--light.button--secondary:hover,
  .button--light.button--secondary:focus {
    color: var(--color-white);
    border: 2px solid var(--color-secondary-light);
    background: var(--color-secondary-light);
    outline: none; /* Ensures no outline is shown when the button is focused */
    box-shadow: none;
  }
  
  .button--light.button--tertiary {
    color: var(--color-primary-light);
  }
  
  .button--light.button--tertiary:hover,
  .button--light.button--tertiary:focus {
    border-bottom: 2px solid var(--color-primary-light);
  }
  
  .button--light.button--primary:disabled,
  .button--light.button--primary:disabled:hover,
  .button--light.button--primary:disabled:focus {
    background-color: var(--color-disabled-light);
  }
  
  .button--light.button--secondary:disabled,
  .button--light.button--secondary:disabled:hover,
  .button--light.button--secondary:disabled:focus {
    border: 2px solid var(--color-disabled-light);
    color: var(--color-disabled-light);
  }
  
  .button--light.button--tertiary:disabled,
  .button--light.button--tertiary:disabled:hover,
  .button--light.button--tertiary:disabled:focus {
    color: var(--color-disabled-light);
  }
  
