  /* Button Size Styles */
  
  .button--small {
    padding: 8px 30px;
    font-size: 0.8rem;
  }
  
  .button--medium {
    padding: 10px 30px;
    font-size: 0.95rem;
    outline: none
  }
  
  .button--large {
    padding: 10px 80px;
    font-size: 1.2rem;
  }